.heading {
  font-size: var(--size-lg);
  font-weight: var(--fw-weight-bold);
  line-height: 50px;
  letter-spacing: 0.5em;
  color: var(--color-casablanca);
}

.heading-group {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6.86px;
}

.text-primary {
  font-size: 1.6rem;
  font-weight: var(--fw-normal);
  color: var(--color-white);
  line-height: 2.6rem;
  color: var(--color-mirage-rgb);
}

.footer-heading {
  font-size: var(--size-lg);
  line-height: 32px;
  color: var(--color-white);
}
